import { createRouter, createWebHashHistory } from "vue-router";
import Home from "../view/Home.vue";
import Biography from "../view/Biography.vue";
import Teory from "../view/Teory.vue";
import Test from "../view/Test.vue";
import Practice from "../view/Practice.vue";
import Introduction from "../view/Introduction.vue";


const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/bio/:id",
    name: "Biography",
    component: Biography,
    props: true,
  },
  {
    path: "/teory",
    name: "Teory",
    component: Teory,
  },
  {
    path: "/practice",
    name: "Practice",
    component: Practice,
  },
  {
    path: "/test",
    name: "Test",
    component: Test,
  },
  {
    path: "/introduction",
    name: "Introduction",
    component: Introduction,
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
