<template>
  <header>
    <nav class="conteiner">
      <div class="logo">
        <p>УО "НГАЭК"</p>
      </div>
      <div v-show="!mobile" class="nav-links">
        <router-link :to="{ name: 'Home' }">Главная страница</router-link>
        <router-link :to="{ name: 'Introduction' }">Введение</router-link>
        <router-link :to="{ name: 'Teory' }"
          >Теоретический материал</router-link
        >
        <router-link :to="{ name: 'Practice' }"
          >Практический материал</router-link
        >
        <router-link :to="{ name: 'Test' }">Контроль знаний</router-link>
      </div>
      <font-awesome-icon
        icon="bars"
        class="menu-icon"
        v-show="mobile"
        @click="toggleMobileNav"
      />
      <transition name="mobile-nav">
        <div class="mobile-nav" v-show="mobile" v-if="mobileNav">
          <router-link :to="{ name: 'Home' }">Главная страница</router-link>
          <router-link :to="{ name: 'Introduction' }">Введение</router-link>
          <router-link :to="{ name: 'Teory' }"
            >Теоретический материал</router-link
          >
          <router-link :to="{ name: 'Practice' }"
            >Практический материал</router-link
          >
          <router-link :to="{ name: 'Test' }">Контроль знаний</router-link>
        </div>
      </transition>
    </nav>
  </header>
</template>

<script>
export default {
  data() {
    return {
      mobile: null,
      mobileNav: null,
      windowWidth: null,
    };
  },
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },
  methods: {
    checkScreen() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth <= 1150) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      this.mobileNav = false;
      return;
    },

    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
    },
  },
};
</script>

